export type MusicMood = "romantic" | "joyful" | "warm" | "festive" | "peaceful"
export type MusicSegment = "entrance" | "tea" | "dining" | "farewell"

export interface Track {
  id: string
  title: string
  artist: string
  segment: MusicSegment
  mood: MusicMood
  duration: number // 
  priority: number // 1-10
  votes: number
}

